@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

body{
    @apply 
        bg-fixed
        bg-gradient-to-b from-gray-200 to-gray-400
        w-full h-full p-0 pt-6 sm:p-6 flex flex-col gap-2
}

.h1{
    @apply
        uppercase
        text-base
}

.info{
    @apply
        border-2 border-gray-400 rounded-lg bg-gray-300 p-1 px-2
}

textarea{
    @apply
        border  border-gray-300
        rounded 
        resize-none hover:resize-y
        px-2
        
}

input{
    @apply
        border border-gray-300
        rounded
}

.btn{
    @apply
        border border-slate-800
        tracking-widest
        text-xs uppercase
        py-1 px-2
        flex gap-2 justify-center items-center
        hover:bg-slate-700
        hover:text-white
        transition
        cursor-pointer
}